* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html {
    background: #f5f5f5;
    font-family: Roboto;
}

.hebrew {
    direction: rtl;
    font-family: "SBL Biblit";
    font-size: x-large;
    text-align: right;
}

chunk verse {
    position: relative;
    top: -5px;
    font-size: x-small;
    color: red;
}